.gallery {
  width: 100%;
  height: auto;
  background-color: #ffffff; }
  .gallery__container {
    margin: 0px auto;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    padding-top: 32px;
    padding-bottom: 32px; }
    @media (max-width: 576px) {
      .gallery__container {
        flex-direction: column;
        align-items: center; } }
  .gallery__media__container {
    position: relative;
    margin: 16px;
    border-radius: 8px;
    overflow: hidden;
    cursor: zoom-in; }
    .gallery__media__container:hover .gallery__media__backdrop {
      background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0, rgba(0, 0, 0, 0.199) 3.5%, rgba(0, 0, 0, 0.195) 7%, rgba(0, 0, 0, 0.19) 10.35%, rgba(0, 0, 0, 0.182) 13.85%, rgba(0, 0, 0, 0.174) 17.35%, rgba(0, 0, 0, 0.165) 20.85%, rgba(0, 0, 0, 0.155) 24.35%, rgba(0, 0, 0, 0.145) 27.85%, rgba(0, 0, 0, 0.135) 31.35%, rgba(0, 0, 0, 0.126) 34.85%, rgba(0, 0, 0, 0.118) 38.35%, rgba(0, 0, 0, 0.11) 41.85%, rgba(0, 0, 0, 0.105) 45.35%, rgba(0, 0, 0, 0.1) 48.85%, rgba(0, 0, 0, 0.103) 52.35%, rgba(0, 0, 0, 0.112) 55.85%, rgba(0, 0, 0, 0.126) 59.35%, rgba(0, 0, 0, 0.144) 62.85%, rgba(0, 0, 0, 0.165) 66.35%, rgba(0, 0, 0, 0.188) 69.85%, rgba(0, 0, 0, 0.213) 73.35%, rgba(0, 0, 0, 0.237) 76.85%, rgba(0, 0, 0, 0.262) 80.35%, rgba(0, 0, 0, 0.285) 83.85%, rgba(0, 0, 0, 0.306) 87.35%, rgba(0, 0, 0, 0.324) 90.85%, rgba(0, 0, 0, 0.338) 94.35%, rgba(0, 0, 0, 0.347) 97.85%, rgba(0, 0, 0, 0.35)); }
  .gallery__media__backdrop {
    width: 100%;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-in; }
  .gallery__media__type {
    padding: 8px; }
    .gallery__media__type .material-icons {
      color: #fff; }
  .gallery__media {
    width: 300px;
    height: 400px;
    object-fit: cover; }
    @media (max-width: 576px) {
      .gallery__media {
        width: 100%;
        height: 300px; } }
  .gallery__filter {
    width: 50px;
    height: 50px;
    position: fixed;
    right: 0;
    bottom: 0;
    margin: 16px 32px;
    background-color: #ff6348;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    cursor: pointer;
    transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transform: translateY(100%); }
    @media (max-width: 576px) {
      .gallery__filter {
        margin: 16px; } }
    .gallery__filter--visible {
      transform: translateY(0%); }
  .gallery__tag__container__backdrop {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    cursor: pointer; }
    .gallery__tag__container__backdrop--visible {
      display: flex;
      align-items: flex-end;
      background-color: rgba(0, 0, 0, 0.5); }
  .gallery__tag__container {
    width: 100%;
    height: 64px;
    background-color: #ffffff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border-top: 1px solid #dfe1e5;
    transform: translateY(100%);
    display: -webkit-box;
    align-items: center;
    overflow-x: auto; }
    .gallery__tag__container::-webkit-scrollbar {
      display: none; }
    .gallery__tag__container--visible {
      transform: translateY(0%); }
  .gallery__tag__heading {
    font-family: 'Open Sans',sans-serif !important;
    font-weight: 700;
    font-size: 18px;
    margin-right: 12px;
    margin-left: 8px;
    color: #333648; }
  .gallery__tag {
    background-color: #e3e8ef;
    color: #333648;
    border-radius: 1000px;
    margin-right: 8px;
    margin-left: 8px;
    min-width: 100px;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    justify-content: center; }
